.GameScreen {
  width: 100vw;
  height: 93vh;
  position: abosule;
}
.StreetView {
  width: 100%;
  height: 100%;
}
.MapWrapper {
  position: absolute;
  z-index: 100;
  top: 38%;
  left: 66%;
  width: 30%;
  height: 60%;
}

.Map {
  width: 100%;
  height: 100%;

  transition: transform 3s ease-out;
}

.Open {
  transform: translateX(0);
}

.Close {
  transform: translateX(-100%);
}
